import "./App.css";
import { HashRouter as Router, Route, Routes } from "react-router-dom";

import Homepage from "./Screens/Homepage/Homepage";
import Shows from "./Screens/TvShows/Shows";
import Movies from "./Screens/Movies/Movies";
import Series from "./Screens/Series/Series";
import Sigin from "./Screens/Authentication/Sigin";
import Login from "./Screens/Authentication/Login";
import VideoContent from "./Screens/DynamicPages/VideoContent";
import { LoginContext } from "./LoginContext";
import { useState, useEffect } from "react";

const userId = localStorage?.getItem("UserId");

function App() {
  const [login, setLogin] = useState(userId !== null);

  useEffect(() => {
    if (userId !== null) {
      setLogin(true);
    } else {
      setLogin(false);
    }
  }, [userId]);

  return (
    <div className="App">
      <LoginContext.Provider value={{ login, setLogin }}>
        <Router>
          <Routes>
            {" "}
            <Route path="/" element={<Homepage />} />
            <Route path="/shows" element={<Shows />} />
            <Route path="/series" element={<Series />} />
            <Route path="/movies" element={<Movies />} />
            <Route path="/video/:videoId" element={<VideoContent />} />
            <Route path="/sign-up" element={<Sigin />} />
            <Route path="/log-in" element={<Login />} />
          </Routes>
        </Router>
      </LoginContext.Provider>
    </div>
  );
}

export default App;
