import React from "react";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import { mobile } from "../../responsive";
import { tab } from "../../responsive";

const Container = styled.div`
  margin: 12px;
  height: 290px;
  width: 190px;
  overflow: hidden;
  border-radius: 20px;
  box-shadow: 0px 10px 15px #e8e8e8;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  &:hover {
    transform: scale(1.03);
    box-shadow: 0px 10px 15px #c4c4c4;
  }
  ${mobile({ height: "250px", width: "160px" })}
`;
const ImageContainer = styled.div`
  flex: 5;
  background-color: gray;
`;
const Text = styled.div`
  padding: 5px;
  flex: 1;
`;

const Title = styled.h1`
  margin: 0;
  margin-left: 5px;
  font-size: 15px;
  text-align: left;
  text-transform: capitalize;
  font-family: "Montserrat";
  ${mobile({ fontSize: "10px" })}
`;

const Subtext = styled.h1`
  flex: 1;
  padding: 0;
  margin: 0;
  color: rgba(185, 185, 185, 1);
  font-size: 10px;
  font-family: "Product Sans";
  font-weight: 300;
`;

const Category = styled.div`
  border-radius: 5px;
  border: 1px solid;
  padding: 3px;
  color: rgba(185, 185, 185, 1);
  flex: 1;
  align-items: center;
  width: fit-content;
`;

const MovieCard = ({ MovieTitle, Year, MovieDuration, MovieCategory, id }) => {
  const navigate = useNavigate();
  const videoId = id;

  return (
    <Container
      onClick={() => {
        if (localStorage?.getItem("LoggedIn")) {
          navigate(`/video/:${videoId}`);
        } else {
          navigate(`/sign-up`);
        }
      }}
    >
      <ImageContainer></ImageContainer>
      <Text>
        <Title>{MovieTitle}</Title>
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            flex: "1",
            alignItems: "center",
          }}
        >
          <Subtext>{Year}</Subtext>
          <Subtext>{MovieDuration}</Subtext>

          <Category>
            <Subtext>{MovieCategory}</Subtext>
          </Category>
        </div>
      </Text>
    </Container>
  );
};

export default MovieCard;
