import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";

const NavItem = styled.h1`
  color: ${(props) =>
    props.active === props.name ? "black" : " rgba(185, 185, 185, 1)"};
  font-family: century-gothic, sans-serif;
  font-style: italic;
  font-size: 40px;
  font-weight: 400;
  margin: 0px 25px;
  cursor: pointer;
`;

const PseudoNavbar = ({ active }) => {
  const Links = [
    ["Movies", "/movies"],
    ["Series", "/series"],
    ["Tv Shows", "/shows"],
  ];

  return (
    <>
      {" "}
      {Links.map((item, index) => {
        return (
          <Link to={item[1]} key={index} style={{ textDecoration: "none" }}>
            <NavItem active={active} name={item[0]}>
              {item[0]}
            </NavItem>
          </Link>
        );
      })}
    </>
  );
};

export default PseudoNavbar;
