import React, { useRef, useState, useEffect } from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { ReactComponent as CancelButton } from "../../Images/Icons/CancelButton.svg";
import Button from "../../Components/Button/Button";
import FormBlock from "../../Components/Form/FormBlock";
import { BaseUrl } from "../../ApiUrl";
import { useNavigate } from "react-router-dom";

const Screen = styled.div`
  background-color: rgba(108, 108, 108, 0.5);
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Container = styled.div`
  background-color: white;
  color: black;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  width: 524px;
  height: 594px;
  position: relative;
`;
const LogoDiv = styled.div`
  display: flex;
  margin: 10px;
`;

const TextLight = styled.h1`
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: 400;
  font-size: 36px;
  margin: 0;
`;
const TextHeavy = styled.h1`
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: 700;
  font-size: 36px;
  color: rgba(255, 138, 0, 1);
  margin: 0;
`;
const Formbody = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  width: 100%;
  border-radius: 20px 20px 0px 0px;
  flex: 4;
`;
const FormContent = styled.div`
  width: 80%;
  height: 80%;
`;
const Formfooter = styled.div`
  justify-content: center;
  align-items: center;
  flex: 0.6;
  display: flex;
  background-color: rgba(232, 232, 232, 1);
  border-radius: 0px 0px 20px 20px;
  width: 100%;

  justify-content: center;
  align-items: center;
`;
const Text = styled.h1`
  font-size: 15px;
  font-family: "Product Sans", sans-serif;
  font-weight: 400;
`;

const Login = () => {
  const navigate = useNavigate();
  const USER_REGEX = /^[a-zA-Z][a-zA-Z0-9-_]{3,23}$/;
  const PASSWORD_REGEX = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{8,24}$/;
  const EMAIL_REGEX =
    /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

  const errRef = useRef();

  const [email, setEmail] = useState("");
  const [validEmail, setValidEmail] = useState(false);
  const [emailFocus, setEmailFocus] = useState(false);

  const [password, setPassword] = useState("");
  const [validPwd, setValidPwd] = useState(false);
  const [pwdFocus, setPwdFocus] = useState(false);

  const [errMsg, setErrMsg] = useState(``);
  const [success, setSuccess] = useState(``);

  useEffect(() => {
    errRef.current.focus();
  }, []);

  useEffect(() => {
    const result = EMAIL_REGEX.test(email);
    setValidEmail(result);
  }, [email]);

  useEffect(() => {
    const result = PASSWORD_REGEX.test(password);

    setValidPwd(result);
  }, [password]);

  useEffect(() => {
    setErrMsg("");
  }, [password]);

  //submit to back end

  const handleSubmit = async (e) => {
    e.preventDefault();
    const formdata = new FormData();
    formdata.append("email", email);
    formdata.append("password", password);

    const requestOptions = {
      method: "POST",
      body: formdata,
      redirect: "follow",
    };

    fetch(`${BaseUrl}/userlogin.php`, requestOptions)
      .then((response) => response.text())
      .then(
        (data) => console.log(data)
        // localStorage.setItem("UserId", data.id),
        // localStorage.setItem("LoggedIn", true)
        // navigate(`/`)
      )
      .catch((error) => console.log("error", error));

    setSuccess(true);
  };

  return (
    <Screen>
      <Container>
        <Link to="/">
          {" "}
          <CancelButton
            style={{
              position: "absolute",
              zIndex: "50",
              top: "-16%",
              right: "-18%",
            }}
          />
        </Link>

        <Formbody>
          <LogoDiv>
            <TextLight>Movie</TextLight> <TextHeavy>Hive</TextHeavy>
          </LogoDiv>
          <div
            style={{
              textTransform: "uppercase",
              margin: "40px 0px",
              display: "flex",
              fontFamily: "Montserrat",
              fontSize: "20px",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            Get{" "}
            <h1
              style={{
                fontWeight: "700",
                fontSize: "20px",
                margin: "0px 5px",
              }}
            >
              HD
            </h1>{" "}
            Movies For{" "}
            <h1
              style={{
                fontWeight: "700",
                fontSize: "20px",
                margin: "0px 5px",
              }}
            >
              Free
            </h1>
          </div>
          <FormContent>
            <FormBlock
              innerRef={errRef}
              width="90%"
              FormLabel="Email"
              placeholder="name@gmail.com"
              onChange={(e) => setEmail(e.target.value)}
              required
              ariaErrorMessage="inValid Name"
              onFocus={() => setEmailFocus(true)}
              onBlur={() => setEmailFocus(false)}
              setErrorMessage={
                !EMAIL_REGEX.test(email) && !emailFocus && email !== ""
              }
              ErrorMessage="Input Valid Email "
            />
            <FormBlock
              width="90%"
              FormLabel="password"
              placeholder="password"
              onChange={(e) => setPassword(e.target.value)}
              required
              ariaErrorMessage="inValid Password"
              type="password"
              onFocus={() => setPwdFocus(true)}
              onBlur={() => setPwdFocus(false)}
              setErrorMessage={
                !PASSWORD_REGEX.test(password) && !pwdFocus && password !== ""
              }
              ErrorMessage="your password should contain atleast 8 charaters, a Capital letter, small letter and number"
            />

            <div style={{ marginTop: "15px" }}></div>
            <Button
              onClick={handleSubmit}
              Text="Log in"
              width="100%"
              padding="10px 0px"
            />
          </FormContent>
        </Formbody>
        <Formfooter>
          <Text>Don't have an account?</Text>
          <Link
            to="/sign-up"
            style={{ textDecoration: "none", color: "rgba(255, 138, 0, 1)" }}
          >
            <Text> Sign Up</Text>
          </Link>
        </Formfooter>
      </Container>
    </Screen>
  );
};

export default Login;
