import React, { useState } from "react";
import styled from "styled-components";
import { ReactComponent as SearchIconSmall } from "../../Images/Icons/SearchIconSmall.svg";

const Container = styled.div`
  flex: 2;
  display: flex;
  align-items: center;
`;

const Bar = styled.input`
  height: ${(props) => props.height};
  width: ${(props) => props.width};
  border-radius: 25px;
  border: none;
  padding: 0px 20px;
  font-family: century-gothic, sans-serif;
  font-size: ${(props) => props.fontSize};
  box-shadow: ${(props) =>
    props.boxShadow ? "0px 10px 15px #e8e8e8" : "none"};

  &::placeholder {
    color: rgba(186, 186, 186, 1);
    border: none;
    font-weight: 200;
    font-style: italic;
  }
  &:focus {
    outline: none;
  }
`;
const SearchButton = styled.div`
  border: none;
  background-color: none;
  cursor: pointer;
  align-content: flex-end;
  margin-left: -10%;
`;

const SearchBar = ({
  width,
  height,
  boxShadow = true,
  fontSize = "16px",
  searchicon = false,
  onChange,
}) => {
  return (
    <Container searchicon>
      <Bar
        width={width}
        height={height}
        boxShadow={boxShadow}
        fontSize={fontSize}
        placeholder="Search here..."
        onChange={onChange}
      />
      {searchicon ? (
        <SearchButton>
          <SearchIconSmall />
        </SearchButton>
      ) : null}
    </Container>
  );
};

export default SearchBar;
