import React, { useEffect, useState } from "react";
import styled from "styled-components";
import Footer from "../../Components/Footer/Footer";
import TopNav from "../../Components/Navbar/TopNav";
import Button from "../../Components/Button/Button";
import { useParams } from "react-router-dom";
import axios from "axios";
import ApiResults from "../../Components/ApiResults/ApiResults";
import SkeletonDetails from "../../Components/SkeletonLoaderComponents/SkeletonDetails";

const Main = styled.div``;

const Body = styled.div`
  width: 100%;

  margin-left: auto;
  margin-right: auto;
  min-height: 100px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-left: 2.5%;
`;
const MovieDetails = styled.div`
  display: flex;
  align-items: flex-start;
  align-items: center;
  flex-direction: column;
  margin-bottom: 200px;
`;
const BackDrop = styled.div`
  width: 100%;
  height: 600px;
  background-color: lightgray;
`;

const InfoContainer = styled.div`
  width: 80%;
  height: 400px;
  background-color: white;
  position: absolute;
  bottom: 0;
  margin-bottom: -10%;
  border-radius: 25px;
  box-shadow: 0px 10px 15px #c4c4c4;
  display: flex;

  justify-content: space-between;
  padding: 20px;
`;
const InfoContent = styled.div`
  margin-top: 10px;
  display: flex;
  width: 100%;
  height: 100%;
`;
const PictureContainer = styled.img`
  flex: 1;
  width: 190px;
  height: 270px;
  background-color: gray;
  border-radius: 20px;
`;
const InfoText = styled.div`
  flex: 4;
  height: 350px;
  margin-left: 20px;
  text-align: left;
`;
const Title = styled.h1`
  margin: 0;
  font-size: 35px;
  text-align: left;
  text-transform: capitalize;
  font-family: "Montserrat";
`;
const MovieSummary = styled.h2`
  font-size: 14px;
  font-family: "Montserrat";
  text-align: left;
  font-weight: 300;
  margin: 0;
  margin-top: 20px;
  width: 90%;
`;
const ProductDetails = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  width: 100%;
`;
const DetailBlock = styled.div`
  flex: 1;
  margin: 0;
  padding: 0;
`;
const DetailSection = styled.div`
  display: flex;
  width: 43%;
  margin-bottom: 20px;
  text-align: left;
`;
const Header = styled.h3`
  margin: 0;
  font-size: 14px;
  font-family: "Montserrat";
  font-weight: bold;
`;
const Detail = styled.h3`
  font-size: 14px;
  font-family: "Montserrat";
  text-align: left;
  font-weight: 300;
  margin: 0;
  margin-left: 5px;
`;
const Buttons = styled.div`
  margin-top: 20px;
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const VideoContent = () => {
  const [videoDetail, setVideoDetail] = useState([]);
  const [showPaignation] = useState(false);
  const [numberOfCards] = useState(12);
  const [loading, setLoading] = useState(false);
  const params = useParams();
  const VideoId = params.videoId.slice(1);
  const scrollToTop = () => {
    window.scrollTo({
      top: 90,
      behavior: "smooth",
    });
  };

  useEffect(() => {
    const fetchPosts = async () => {
      setLoading(true);
      const res = await axios.get(
        `https://jsonplaceholder.typicode.com/posts/${VideoId}`
      );
      setVideoDetail(res.data);
      setLoading(false);
    };
    fetchPosts();
    scrollToTop();
  }, [VideoId]);

  return (
    <Main>
      <TopNav />
      {loading ? (
        <SkeletonDetails />
      ) : (
        <MovieDetails>
          <BackDrop />
          <InfoContainer>
            <InfoContent>
              {" "}
              <PictureContainer />
              <InfoText>
                <Title>{videoDetail.title}</Title>
                <MovieSummary>{videoDetail.body}</MovieSummary>
                <ProductDetails>
                  <DetailBlock>
                    <DetailSection>
                      {" "}
                      <Header>Release Date :</Header>
                      <Detail> 2019-05-05</Detail>
                    </DetailSection>
                    <DetailSection>
                      {" "}
                      <Header>Genre :</Header>
                      <Detail> Action, History</Detail>
                    </DetailSection>
                    <DetailSection>
                      {" "}
                      <Header>Duration :</Header>
                      <Detail> 1hr 50mins</Detail>
                    </DetailSection>
                  </DetailBlock>
                  <DetailBlock>
                    <DetailSection>
                      {" "}
                      <Header>Size:</Header>
                      <Detail>500MB</Detail>
                    </DetailSection>
                  </DetailBlock>
                </ProductDetails>
                <Buttons>
                  <Button
                    Text="Stream Now"
                    borderRadius="18px"
                    fontWeight="300"
                    Svg="Play"
                  />
                  <Button
                    padding="10px"
                    Text="Download"
                    borderRadius="18px"
                    fontWeight="300"
                    Svg="Download"
                  />
                </Buttons>
              </InfoText>
            </InfoContent>
          </InfoContainer>
        </MovieDetails>
      )}

      <Body>
        <ApiResults
          Description=" Related"
          showPaignation={showPaignation}
          numberOfCards={numberOfCards}
        />
      </Body>
      <Footer />
    </Main>
  );
};

export default VideoContent;
