import React, { useState } from "react";
import styled from "styled-components";
import { ReactComponent as Next } from "../../Images/Icons/Next.svg";
import { ReactComponent as Previous } from "../../Images/Icons/Previous.svg";

const Body = styled.nav`
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0px;
`;

const Paig = styled.div`
  display: flex;
  align-items: center;
  font-size: 20px;
  width: fit-content;
`;

const PaignItem = styled.h1`
  color: "black";
  cursor: pointer;
  font-size: 20px;
  font-family: "Product Sans", sans-serif;
  margin: 0px 20px;
`;
const PaigLink = styled.div`
  border-radius: 50%;
  background-color: ${(props) =>
    props.page === props.index ? "orange" : null};
  color: ${(props) => (props.page === props.index ? "white" : null)};
  padding: 20px;
  height: 20px;
  width: 20px;
`;

const Paignation = ({
  loading,
  postsPerPage,
  totalPosts,
  paignate,
  currentPage,
}) => {
  const pageNumbers = [];

  for (let i = 1; i <= Math.ceil(totalPosts / postsPerPage); i++) {
    pageNumbers.push(i);
  }

  return (
    <Body>
      {currentPage > 1 ? (
        <Previous
          style={{ cursor: "pointer" }}
          onClick={() => {
            if (currentPage > 1) {
              paignate(currentPage - 1);
            }
          }}
        />
      ) : null}

      <Paig>
        {pageNumbers.map((number) => {
          return (
            <PaignItem key={number}>
              <PaigLink
                page={currentPage}
                index={number}
                onClick={() => paignate(number)}
              >
                {number}
              </PaigLink>
            </PaignItem>
          );
        })}
        {currentPage >= 1 ? (
          <div style={{ cursor: "pointer" }}>
            <Next
              onClick={() => {
                if (currentPage < pageNumbers.length) {
                  paignate(currentPage + 1);
                }
              }}
            />
          </div>
        ) : null}
      </Paig>
    </Body>
  );
};

export default Paignation;
