import React from "react";
import styled from "styled-components";
import { keyframes } from "styled-components";
const SkeletonLoading = keyframes`
    0%{
        background-color :lightgray
    }
    100%{
        background-color : rgb(182, 181, 181)
    }
    
`;
const SkeletonImage = keyframes`
    0%{
        background-color :lightgray
    }
    100%{
        background-color :rgb(182, 181, 181)
    }
    
`;
const SkeletonText = keyframes`
    0%{
        background-color :lightgray
    }
    100%{
        background-color :rgb(182, 181, 181)
    }
    
`;

const MovieDetails = styled.div`
  display: flex;
  align-items: flex-start;
  align-items: center;
  flex-direction: column;
  margin-bottom: 200px;
`;
const BackDrop = styled.div`
  width: 100%;
  height: 600px;
  background-color: lightgray;
  animation-name: ${SkeletonLoading};
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
`;

const InfoContainer = styled.div`
  width: 80%;
  height: 400px;
  background-color: white;
  position: absolute;
  bottom: 0;
  margin-bottom: -10%;
  border-radius: 25px;
  box-shadow: 0px 10px 15px #c4c4c4;
  display: flex;

  justify-content: space-between;
  padding: 20px;
`;
const InfoContent = styled.div`
  margin-top: 10px;
  display: flex;
  width: 100%;
  height: 100%;
`;
const PictureContainer = styled.img`
  flex: 1;
  width: 190px;
  height: 270px;
  background-color: gray;
  border-radius: 20px;
  animation-name: ${SkeletonImage};
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
`;
const InfoText = styled.div`
  flex: 4;
  height: 350px;
  margin-left: 20px;
  text-align: left;
`;
const Title = styled.h1`
  margin: 0;
  height: 50px;
  text-align: left;
  text-transform: capitalize;
  font-family: "Montserrat";
  width: 400px;
  border-radius: 5px;
  background-color: lightgray;
  animation-name: ${SkeletonText};
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
`;
const MovieSummary = styled.h2`
  text-align: left;
  font-weight: 300;
  margin: 0;
  margin-top: 20px;
  width: 90%;
`;
const Text = styled.div`
  height: 15px;
  width: 100%;
  background-color: lightgray;
  border-radius: 5px;
  margin: 10px;
  animation-name: ${SkeletonText};
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
`;
const ProductDetails = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  width: 100%;
`;
const DetailBlock = styled.div`
  flex: 1;
  margin: 0;
  padding: 0;
`;
const DetailSection = styled.div`
  display: flex;
  width: 43%;
  margin-bottom: 20px;
  text-align: left;
`;
const Header = styled.h3`
  margin: 0;
  height: 14px;
  font-family: "Montserrat";
  font-weight: bold;
  background-color: #b3b0b0;
  width: 100px;
  border-radius: 5px;
  animation-name: ${SkeletonText};
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
`;
const Detail = styled.h3`
  margin: 0;
  height: 14px;
  background-color: lightgray;
  width: 100px;
  border-radius: 5px;
  margin: 0;
  margin-left: 5px;
  animation-name: ${SkeletonText};
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
`;
const Buttons = styled.div`
  margin-top: 20px;
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;
const Button = styled.div`
  border-radius: 25px;
  height: 40px;
  background-color: lightgray;
  width: 120px;
  animation-name: ${SkeletonText};
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
`;
const SkeletonDetails = () => {
  return (
    <MovieDetails>
      <BackDrop />
      <InfoContainer>
        <InfoContent>
          {" "}
          <PictureContainer />
          <InfoText>
            <Title></Title>
            <MovieSummary>
              <Text /> <Text /> <Text /> <Text />
            </MovieSummary>
            <ProductDetails>
              <DetailBlock>
                <DetailSection>
                  {" "}
                  <Header></Header>
                  <Detail> </Detail>
                </DetailSection>
                <DetailSection>
                  {" "}
                  <Header></Header>
                  <Detail> </Detail>
                </DetailSection>
                <DetailSection>
                  {" "}
                  <Header></Header>
                  <Detail> </Detail>
                </DetailSection>
              </DetailBlock>
              <DetailBlock>
                <DetailSection>
                  {" "}
                  <Header></Header>
                  <Detail></Detail>
                </DetailSection>
              </DetailBlock>
            </ProductDetails>
            <Buttons>
              <Button />
              <Button />
            </Buttons>
          </InfoText>
        </InfoContent>
      </InfoContainer>
    </MovieDetails>
  );
};

export default SkeletonDetails;
