import React from "react";
import styled from "styled-components";
import { mobile } from "../../responsive";

const Container = styled.div`
  display: flex;
  width: 100%;
  height: 162px;
  background-color: black;
`;
const NameDiv = styled.div`
  display: flex;
  flex: 0.6;
  margin: 5px 10px;
  flex-direction: column;
  text-align: left;
`;

const Logo = styled.div`
  display: flex;
`;
const TextLight = styled.h1`
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: 400;
  font-size: 24px;
  color: white;
  ${mobile({ fontSize: "15px" })}
`;
const Logosmall = styled.h1`
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: 400;
  font-size: 12px;
  color: white;
  margin-top: -5px;
  ${mobile({ fontSize: "10px" })}
`;
const TextHeavy = styled.h1`
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: 700;
  font-size: 24px;
  color: rgba(255, 138, 0, 1);
  ${mobile({ fontSize: "15px" })}
`;
const Description = styled.div`
  flex: 5;
  text-align: left;
  margin: 20px 5px;
  ${mobile({ margin: "10px 2px" })}
`;
const DescText = styled.h1`
  font-size: 15px;
  color: white;
  width: 90%;
  font-weight: 300;
  font-family: "Product Sans", sans-serif;
  ${mobile({ fontSize: "10px" })}
`;
const Disclaimer = styled.div`
  border-radius: 10px;
  border: 1px solid rgba(138, 138, 138, 1);
  width: fit-content;
  height: fit-content;
  padding: 2px 4px;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  position: relative;
  ${mobile({ marginTop: "10px", padding: "1px 2px" })}
`;
const DiscText = styled.h1`
  font-size: 13px;
  color: rgba(138, 138, 138, 1);
  font-weight: lighter;
  ${mobile({ fontSize: "10px" })}
`;
const Redball = styled.div`
  border-radius: 50%;
  background-color: red;
  width: 10px;
  height: 10px;
  position: absolute;
  top: 0;
  right: 0;
  margin-top: -4px;
  margin-right: -2px;
`;
const Footer = () => {
  return (
    <Container>
      <NameDiv>
        <Logo>
          <TextLight>Movie</TextLight> <TextHeavy>Hive</TextHeavy>
        </Logo>{" "}
        <Logosmall>MovieHive</Logosmall>
      </NameDiv>
      <Description>
        <DescText>
          MOVIEHIVE is a Free Movies downloading and streaming site. We let you
          watch movies online without having to register or paying, with over
          10000 movies, TV-Series and Shows. You can also Download full movies
          from MOVIEHIVE and watch it later if you want.
        </DescText>
        <Disclaimer>
          <DiscText>
            MOVIEHIVE does not store any files on our server, we only linked to
            the media which is hosted on 3rd party services.
          </DiscText>
          <Redball />
        </Disclaimer>
      </Description>
    </Container>
  );
};

export default Footer;
