import React, { useState } from "react";

import Footer from "../../Components/Footer/Footer";
import TopNav from "../../Components/Navbar/TopNav";

import styled from "styled-components";
import CategoryBlock from "../../Components/Category/CategoryBlock";
import ApiResults from "../../Components/ApiResults/ApiResults";

const Body = styled.div`
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  min-height: 100px;
  padding: 10px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  padding-left: 2.5%;
`;
const Homepage = () => {
  const [showPaignation] = useState(false);
  const [numberOfCards] = useState(12);
  return (
    <>
      <TopNav active="Home" />
      <Body>
        <ApiResults
          Description=" Trending"
          category="Movies & Series"
          showPaignation={showPaignation}
          numberOfCards={numberOfCards}
        />
        <ApiResults
          Description=" Latest Releases"
          category="Movies & Series"
          showPaignation={showPaignation}
          numberOfCards={numberOfCards}
        />
        <ApiResults
          Description=" Coming Soon"
          category="Movies & Series"
          showPaignation={showPaignation}
          numberOfCards={numberOfCards}
        />
      </Body>

      <Footer />
    </>
  );
};

export default Homepage;
