import React from "react";
import styled from "styled-components";

const Block = styled.div`
  margin-left: auto;
  margin-right: auto;
  height: fit-content;
`;

const Lable = styled.h1`
  color: rgba(183, 183, 183, 1);
  font-size: 12px;
  font-weight: 400;
  font-family: "Montserrat";
  margin-bottom: 10px;
  text-align: left;
  text-transform: uppercase;
`;

const Input = styled.input`
  padding: 0;
  height: ${(props) => props.height};
  width: ${(props) => props.width};
  border-radius: 5px;
  padding: 0px 20px;
  font-family: "Montserrat";
  font-size: 12px;
  background-color: rgba(243, 243, 243, 1);
  border: none;
  margin-bottom: 10px;
  &::placeholder {
    color: rgba(186, 186, 186, 1);
    border: none;
    font-weight: 200;
    font-style: italic;
  }
  &:focus {
    outline: none;
    background-color: white;
    border: 1px solid rgba(255, 138, 0, 1);
  }
`;
const Error = styled.h1`
  margin-left: 20px;
  color: red;
  font-family: "Montserrat";
  font-size: 12px;
  font-weight: 300;
  text-transform: capitalize;
  margin: 0;
  text-align: left;
`;
const FormBlock = ({
  FormLabel = "Label",
  width = "100%",
  height = "35px",
  placeholder = "Placeholder",
  onChange,
  onFocus,
  onBlur,
  autoComplete,
  required,
  ariaErrorMessage,
  ariaInvalid,
  type,
  ErrorMessage,
  innerRef,
  setErrorMessage,
}) => {
  return (
    <Block>
      <div style={{ display: "flex" }}>
        {" "}
        <Lable>{FormLabel}</Lable>
      </div>

      <Input
        width={width}
        height={height}
        placeholder={placeholder}
        onChange={onChange}
        autoComplete={autoComplete}
        onFocus={onFocus}
        onBlur={onBlur}
        required={required}
        aria-invalid={ariaInvalid}
        aria-errormessage={ariaErrorMessage}
        type={type}
        ref={innerRef}
      />
      {setErrorMessage ? <Error>{ErrorMessage} !</Error> : null}
    </Block>
  );
};

export default FormBlock;
