import React, { useRef, useState, useEffect, useContext } from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { ReactComponent as CancelButton } from "../../Images/Icons/CancelButton.svg";
import Button from "../../Components/Button/Button";
import FormBlock from "../../Components/Form/FormBlock";
import axios from "axios";
import { LoginContext } from "../../LoginContext";
import { BaseUrl } from "../../ApiUrl";
import { useNavigate } from "react-router-dom";

const Screen = styled.div`
  background-color: rgba(108, 108, 108, 0.5);
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Container = styled.div`
  background-color: white;
  color: black;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  width: 524px;
  height: 594px;
  position: relative;
`;
const LogoDiv = styled.div`
  display: flex;
  margin: 10px;
`;

const TextLight = styled.h1`
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: 400;
  font-size: 36px;
  margin: 0;
`;
const TextHeavy = styled.h1`
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: 700;
  font-size: 36px;
  color: rgba(255, 138, 0, 1);
  margin: 0;
`;
const Formbody = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  width: 100%;
  border-radius: 20px 20px 0px 0px;
  flex: 4;
`;
const FormContent = styled.div`
  width: 80%;
  height: 80%;
`;
const Formfooter = styled.div`
  justify-content: center;
  align-items: center;
  flex: 0.6;
  display: flex;
  background-color: rgba(232, 232, 232, 1);
  border-radius: 0px 0px 20px 20px;
  width: 100%;

  justify-content: center;
  align-items: center;
`;
const Text = styled.h1`
  font-size: 15px;
  font-family: "Product Sans", sans-serif;
  font-weight: 400;
`;

const Sigin = () => {
  const navigate = useNavigate();

  const USER_REGEX = /^[a-zA-Z][a-zA-Z0-9-_]{3,23}$/;
  const PASSWORD_REGEX = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{8,24}$/;
  const EMAIL_REGEX =
    /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

  const { login, setLogin } = useContext(LoginContext);

  const userRef = useRef();
  const errRef = useRef();

  const [user, setUser] = useState("");
  const [validName, setValidName] = useState(false);
  const [userFocus, setUserFocus] = useState(false);

  const [email, setEmail] = useState("");
  const [validEmial, setValidEmail] = useState(false);
  const [emailFocus, setEmailFocus] = useState(false);

  const [password, setPassword] = useState("");
  const [validPwd, setValidPwd] = useState(false);
  const [pwdFocus, setPwdFocus] = useState(false);

  const [confirmPwd, setconfirmPwd] = useState("");
  const [validMatch, setValiMatch] = useState(false);
  const [matchFocus, setMatchFocus] = useState(false);

  const [errMsg, setErrMsg] = useState(``);
  const [success, setSuccess] = useState(``);

  useEffect(() => {
    userRef.current.focus();
  }, []);

  useEffect(() => {
    const result = USER_REGEX.test(user);
    setValidName(result);
  }, [user]);

  useEffect(() => {
    const result = EMAIL_REGEX.test(user);
    setValidEmail(result);
  }, [email]);

  useEffect(() => {
    const result = PASSWORD_REGEX.test(password);

    setValidPwd(result);
    const match = password === confirmPwd;
    setValiMatch(match);
  }, [password, confirmPwd]);

  useEffect(() => {
    setErrMsg("");
  }, [user, password, confirmPwd]);

  //submit to back end

  const handleSubmit = async (e) => {
    e.preventDefault();

    const formdata = new FormData();
    formdata.append("name", user);
    formdata.append("email", email);
    formdata.append("password", password);

    const requestOptions = {
      method: "POST",
      body: formdata,
      redirect: "follow",
    };

    const v1 = USER_REGEX.test(user);
    const v2 = PASSWORD_REGEX.test(password);
    if (!v1 || !v2) {
      console.log("Invalid Entry");
      return;
    }

    fetch(`${BaseUrl}/signup.php`, requestOptions)
      .then((response) => response.json())
      .then(
        (data) => localStorage.setItem("UserId", data.id),
        setLogin(true),
        localStorage.setItem("LoggedIn", true)
      )
      .catch((error) => console.log("error", error));
  };

  return (
    <Screen>
      <Container>
        <Link to="/">
          {" "}
          <CancelButton
            style={{
              position: "absolute",
              zIndex: "50",
              top: "-16%",
              right: "-18%",
            }}
          />
        </Link>

        <Formbody>
          <LogoDiv>
            <TextLight>Movie</TextLight> <TextHeavy>Hive</TextHeavy>
          </LogoDiv>
          <h1
            style={{
              fontSize: "20px",
              fontWeight: "400",
              fontFamily: "Montserrat",
              textTransform: "uppercase",
            }}
          >
            Create an Account
          </h1>
          <FormContent>
            <FormBlock
              width="90%"
              FormLabel="Your name"
              placeholder="name"
              innerRef={userRef}
              onChange={(e) => setUser(e.target.value)}
              required
              ariaInvalid={validName ? "false" : "true"}
              ariaErrorMessage="inValid Name"
              onFocus={() => setUserFocus(true)}
              onBlur={() => setUserFocus(false)}
              setErrorMessage={
                !USER_REGEX.test(user) && !userFocus && user !== ""
              }
              ErrorMessage="User name should be more than 3 words "
            />
            <FormBlock
              width="90%"
              FormLabel="Email"
              placeholder="name@gmail.com"
              onChange={(e) => setEmail(e.target.value)}
              required
              ariaInvalid={validName ? "false" : "true"}
              ariaErrorMessage="inValid Name"
              onFocus={() => setEmailFocus(true)}
              onBlur={() => setEmailFocus(false)}
              setErrorMessage={
                !EMAIL_REGEX.test(email) && !emailFocus && email !== ""
              }
              ErrorMessage="Input Valid Email "
            />
            <FormBlock
              width="90%"
              FormLabel="password"
              placeholder="password"
              onChange={(e) => setPassword(e.target.value)}
              required
              ariaInvalid={validName ? "false" : "true"}
              ariaErrorMessage="inValid Password"
              type="password"
              onFocus={() => setPwdFocus(true)}
              onBlur={() => setPwdFocus(false)}
              setErrorMessage={
                !PASSWORD_REGEX.test(password) && !pwdFocus && password !== ""
              }
              ErrorMessage="your password should contain atleast 8 charaters, a Capital letter, small letter and number"
            />
            <FormBlock
              width="90%"
              FormLabel="Confirm password"
              placeholder="password"
              onChange={(e) => setconfirmPwd(e.target.value)}
              required
              ariaInvalid={validName ? "false" : "true"}
              ariaErrorMessage="inValid Password"
              type="password"
              onFocus={() => setMatchFocus(true)}
              onBlur={() => setMatchFocus(false)}
              setErrorMessage={
                password !== confirmPwd && !matchFocus && confirmPwd !== ""
              }
              ErrorMessage="Passwords are not the same"
            />
            <div style={{ marginTop: "15px" }}></div>
            <Button
              onClick={handleSubmit}
              Text="Sign Up"
              width="100%"
              padding="10px 0px"
            />
          </FormContent>
        </Formbody>
        <Formfooter>
          <Text>Have an account?</Text>
          <Link
            to="/log-in"
            style={{ textDecoration: "none", color: "rgba(255, 138, 0, 1)" }}
          >
            <Text> Log In</Text>
          </Link>
        </Formfooter>
      </Container>
    </Screen>
  );
};

export default Sigin;
