import React, { useState, useEffect } from "react";

import Footer from "../../Components/Footer/Footer";
import TopNav from "../../Components/Navbar/TopNav";

import styled from "styled-components";
import CategoryBlock from "../../Components/Category/CategoryBlock";
import PseudoNavbar from "../../Components/PseudoNavbar/PseudoNavbar";
import axios from "axios";
import Paignation from "../../Components/Paignation/Paignation";
import ApiResults from "../../Components/ApiResults/ApiResults";

const Body = styled.div`
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  min-height: 100px;
  padding: 10px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  padding-left: 2.5%;
`;
const LinkDiv = styled.div`
  width: 90%;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
`;
const Link = styled.h1`
  color: ${(props) =>
    props.active === props.name ? "black" : " rgba(185, 185, 185, 1)"};
  font-family: century-gothic, sans-serif;
  font-style: italic;
  font-size: 40px;
  font-weight: 400;
  margin: 0px 25px;
  cursor: pointer;
`;

const Movies = () => {
  const [showPaignation] = useState(true);
  const [numberOfCards] = useState(24);
  return (
    <>
      <TopNav active="Movies" />
      <LinkDiv>
        <PseudoNavbar active="Movies" />
      </LinkDiv>
      <Body>
        <ApiResults
          showPaignation={showPaignation}
          numberOfCards={numberOfCards}
        />
      </Body>

      <Footer />
    </>
  );
};

export default Movies;
