import React, { useState, useContext, useEffect } from "react";
import Navbar from "../Navbar/Navbar";
import styled from "styled-components";
import topNavImg from "../../Images/TopNavHome/topNavImg.svg";
import SearchBar from "../SearchBar/SearchBar";
import { ReactComponent as SearchIcon } from "../../Images/Icons/SearchIcon.svg";
import { mobile } from "../../responsive";
import { LoginContext } from "../../LoginContext";

const TopNavigation = styled.div`
  background-color: ${(props) =>
    props.isLoggedIn ? "none" : "rgba(200, 128, 43, 0.5)"};

  height: ${(props) => (props.isLoggedIn ? "fitContent" : "318px")};
  display: flex;
  flex-direction: column;
  overflow: hidden;
  color: white;
`;

const SvgContainer = styled.img`
  position: absolute;
  width: 100%;
  height: fit-content;
  object-fit: cover;
  overflow: hidden;
  z-index: -1;
  ${mobile({ width: "100%" })}
`;
const SearchDiv = styled.div`
  margin-top: -3%;
  margin-left: auto;
  margin-right: auto;
  z-index: 1000;
  width: 60%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  ${mobile({ display: "none" })}
`;
const SearchMobile = styled.div`
  margin-top: -3%;
  margin-left: auto;
  margin-right: auto;
  z-index: 1000;
  width: 60%;
  height: 100px;
  display: none;
  justify-content: space-between;
  align-items: center;
  ${mobile({ display: "flex", width: "90%", marginTop: "40px" })}
`;
const SearchButton = styled.div`
  border: none;
  background-color: none;
  cursor: pointer;
`;

const TopNav = ({ active }) => {
  const { login, setLogin } = useContext(LoginContext);
  const [isLoggedIn, setIsLoggedIn] = useState(
    localStorage?.getItem("LoggedIn")
  );
  const [searchTerm, setSearchTerm] = useState("");

  return (
    <>
      <TopNavigation isLoggedIn={isLoggedIn}>
        <Navbar active={active} isLoggedIn={isLoggedIn} />
        {isLoggedIn ? null : <SvgContainer src={topNavImg} />}
        {isLoggedIn ? null : (
          <SearchMobile>
            <SearchBar
              height="43px"
              width="90%"
              boxShadow={false}
              fontSize="14px"
              searchicon="true"
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </SearchMobile>
        )}
      </TopNavigation>
      {isLoggedIn ? null : (
        <SearchDiv>
          <SearchBar width="90%" height="50px" boxShadow="true" />
          <SearchButton>
            {" "}
            <SearchIcon />
          </SearchButton>
        </SearchDiv>
      )}
    </>
  );
};

export default TopNav;
