import React from "react";
import styled from "styled-components";
import SearchBar from "../SearchBar/SearchBar";
import { ReactComponent as DropdownIcon } from "../../Images/Icons/DropdownIcon.svg";
import { Link } from "react-router-dom";
import { mobile } from "../../responsive";

const Container = styled.nav`
  color: white;
  background-color: ${(props) =>
    props.isLoggedIn ? "rgba(255, 138, 0, 1)" : "none"};
`;
const TopNav = styled.div`
  padding: ${(props) => (props.isLoggedIn ? "10px" : "20px")};
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
`;
const LogoDiv = styled.div`
  display: flex;
  flex: ${(props) => (props.isLoggedIn ? "0.7" : "1.5")};
  ${mobile({ flex: "0", marginRight: "auto", marginLeft: "auto" })}
`;

const TextLight = styled.h1`
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: 400;
  font-size: 20px;
`;
const TextHeavy = styled.h1`
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: 700;
  font-size: 20px;
`;
const Links = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  ${mobile({ display: "none" })}
`;
const NavItem = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  flex: 0.8;
  padding: 0;
`;
const LinkName = styled.h1`
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  font-family: "Montserrat";
`;
const LogIn = styled.div`
  cursor: pointer;
  flex: 0.8;
  height: 30px;
  display: flex;
  border-radius: 10px;
  background-color: white;
  align-items: center;
  justify-content: center;
`;
const LogInText = styled.h1`
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  font-family: "Montserrat";
  color: rgba(255, 138, 0, 1);
`;
const TextDiv = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  margin-top: 3%;

  justify-content: center;
`;
const ProfileDiv = styled.div`
  flex: 0.7;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 20px;
`;
const DropdownButton = styled.div`
  cursor: pointer;
`;
const Profile = styled.div`
  border-radius: 50%;
  background-color: #e2e2e2;
  width: 54px;
  height: 54px;
`;
const TextHeavyTitle = styled.h1`
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: 700;
  font-size: 30px;
  ${mobile({ fontSize: "16px" })}
`;
const TextLightTitle = styled.h1`
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: 400;
  font-size: 30px;
  margin: 0px 10px;
  ${mobile({ fontSize: "16px" })}
`;
const LinkItem = [
  ["Home", "/"],
  ["Shows", "/shows"],
  ["Series", "/series"],
  ["Movies", "/movies"],
];

const UnderLine = () => {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        width: "70%",
        height: "4.8px",
        marginTop: "-5%",
      }}
    >
      <div
        style={{
          width: "60%",
          backgroundColor: "white",
          borderRadius: "10px",
          marginRight: "4px",
        }}
      ></div>
      <div
        style={{
          width: "20%",
          backgroundColor: "white",
          borderRadius: "10px",
        }}
      ></div>
    </div>
  );
};

const NavbarWhenNotLoggedIn = ({ active, isLoggedIn }) => {
  return (
    <Container isLoggedIn={isLoggedIn}>
      <TopNav isLoggedIn={isLoggedIn}>
        {" "}
        <LogoDiv isLoggedIn={isLoggedIn}>
          <Link
            to="/"
            style={{ textDecoration: "none", color: "white", display: "flex" }}
          >
            <TextLight>Movie</TextLight> <TextHeavy>Hive</TextHeavy>
          </Link>
        </LogoDiv>
        <Links>
          {LinkItem.map((item, index) => (
            <Link
              to={item[1]}
              key={index}
              style={{ textDecoration: "none", color: "white", flex: "0.8" }}
            >
              {" "}
              <NavItem key={index}>
                <LinkName>{item[0]}</LinkName>
                {active === item[0] ? <UnderLine /> : null}
              </NavItem>
            </Link>
          ))}
          <Link
            to="/sign-up"
            style={{ textDecoration: "none", color: "white", flex: "0.8" }}
          >
            <NavItem>
              <LinkName>Sign Up</LinkName>
            </NavItem>
          </Link>
          <Link
            to="/log-in"
            style={{ textDecoration: "none", color: "white", flex: "0.8" }}
          >
            {" "}
            <LogIn>
              <LogInText>Log In</LogInText>
            </LogIn>
          </Link>
        </Links>
      </TopNav>

      <TextDiv>
        <TextHeavyTitle> Movies , Series , Shows </TextHeavyTitle>
        <TextLightTitle> And </TextLightTitle>
        <TextHeavyTitle> More </TextHeavyTitle>
      </TextDiv>
    </Container>
  );
};

const NavbarWhenLoggedIn = ({ active, isLoggedIn }) => {
  return (
    <Container isLoggedIn={isLoggedIn}>
      <TopNav isLoggedIn={isLoggedIn}>
        {" "}
        <LogoDiv isLoggedIn={isLoggedIn}>
          <Link
            to="/"
            style={{ textDecoration: "none", color: "white", display: "flex" }}
          >
            <TextLight>Movie</TextLight> <TextHeavy>Hive</TextHeavy>
          </Link>
        </LogoDiv>
        <Links>
          {LinkItem.map((item, index) => (
            <Link
              to={item[1]}
              key={index}
              style={{ textDecoration: "none", color: "white", flex: "0.8" }}
            >
              {" "}
              <NavItem key={index}>
                <LinkName>{item[0]}</LinkName>
                {active === item[0] ? <UnderLine /> : null}
              </NavItem>
            </Link>
          ))}
          <SearchBar
            height="39px"
            width="257px"
            boxShadow={false}
            fontSize="14px"
            searchicon="true"
          />
          <ProfileDiv>
            <DropdownButton>
              <DropdownIcon />
            </DropdownButton>{" "}
            <Profile />
          </ProfileDiv>
        </Links>
      </TopNav>
    </Container>
  );
};

const Navbar = ({ active, isLoggedIn }) => {
  return (
    <>
      {isLoggedIn ? (
        <NavbarWhenLoggedIn active={active} isLoggedIn={isLoggedIn} />
      ) : (
        <NavbarWhenNotLoggedIn active={active} isLoggedIn={isLoggedIn} />
      )}
    </>
  );
};

export default Navbar;
