import React, { useState, useEffect } from "react";
import styled from "styled-components";
import CategoryCard from "../Category/CategoryCard";
import MovieCard from "../MovieCard/MovieCard";
import axios from "axios";
import SkeletonCard from "../SkeletonLoaderComponents/SkeletonCard";
import { mobile } from "../../responsive";

const Container = styled.div`
  margin: 50px 0px;
`;
const TopSection = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: fit-content;
`;
const BlockDescription = styled.h1`
  color: black;
  font-family: century-gothic, sans-serif;
  font-style: italic;
  font-size: 32px;
  font-weight: 400;
  margin: 0;
  margin-right: 20px;
  ${mobile({ fontSize: "20px" })}
`;

const Body = styled.div`
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: fit-content;
`;

const CategoryBlock = ({ posts, loading, Description = "", category = "" }) => {
  if (loading) {
    return (
      <Body>
        <SkeletonCard /> <SkeletonCard /> <SkeletonCard /> <SkeletonCard />{" "}
        <SkeletonCard /> <SkeletonCard /> <SkeletonCard />
        <SkeletonCard /> <SkeletonCard /> <SkeletonCard />
        <SkeletonCard /> <SkeletonCard />
      </Body>
    );
  }
  return (
    <Container>
      {Description === "" ? null : (
        <TopSection>
          <BlockDescription>{Description}</BlockDescription>
          <CategoryCard Text={category} />
        </TopSection>
      )}

      <Body>
        {posts.map((post, index) => {
          return (
            <MovieCard
              id={post.id}
              key={index}
              MovieTitle={post.title}
              Year="2021"
              MovieDuration="1hr 25min"
              MovieCategory="Series"
            />
          );
        })}
      </Body>
    </Container>
  );
};

export default CategoryBlock;
