import React from "react";
import styled from "styled-components";
import { keyframes } from "styled-components";
import { mobile } from "../../responsive";

const SkeletonLoading = keyframes`
    0%{
        background-color :gray
    }
    100%{
        background-color :rgb(162, 160, 160)
    }
    
`;
const SkeletonImage = keyframes`
    0%{
        background-color :lightgray
    }
    100%{
        background-color :rgb(182, 181, 181)
    }
    
`;
const SkeletonText = keyframes`
    0%{
        background-color :lightgray
    }
    100%{
        background-color :rgb(182, 181, 181)
    }
    
`;
const Main = styled.div`
  margin: 12px;
  height: 290px;
  width: 190px;
  overflow: hidden;
  border-radius: 20px;
  box-shadow: 0px 10px 15px #e8e8e8;
  display: flex;
  flex-direction: column;
  background-color: gray;
  box-shadow: 0px 10px 15px #9e9e9e;
  animation-name: ${SkeletonLoading};
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  ${mobile({ height: "264px", width: "175px" })}
`;

const ImageContainer = styled.div`
  flex: 5;
  background-color: lightgray;
  animation-name: ${SkeletonImage};
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
`;
const Text = styled.div`
  padding: 5px;
  flex: 1;
  margin-top: 10px;
`;

const Title = styled.h1`
  margin: 0;
  margin-left: 5px;
  font-size: 15px;
  text-align: left;
  font-family: "Montserrat";
  height: 10px;
  background-color: lightgray;
  animation-name: ${SkeletonText};
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  border-radius: 5px;
`;

const Subtext = styled.h1`
  flex: 1;
  padding: 0;
  margin: 0;
  color: rgba(185, 185, 185, 1);
  font-size: 10px;
  font-family: "Product Sans";
  font-weight: 300;
  height: 10px;
  background-color: lightgray;
  animation-name: ${SkeletonText};
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  border-radius: 5px;
  margin: 10px 5px;
`;

const Category = styled.div`
  border-radius: 5px;

  padding: 3px;
  color: lightgray;
  flex: 1;
  align-items: center;
  width: fit-content;
`;

const SkeletonCard = () => {
  return (
    <Main>
      <ImageContainer></ImageContainer>
      <Text>
        <Title></Title>
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            flex: "1",
            alignItems: "center",
          }}
        >
          <Subtext></Subtext>
          <Subtext></Subtext>

          <Category>
            <Subtext></Subtext>
          </Category>
        </div>
      </Text>
    </Main>
  );
};

export default SkeletonCard;
